.icon-left {
  position: absolute;
  left: 10px;
  top: 10px;
}

.icon-right {
  position: absolute;
  right: 10px;
  bottom: 10px;
}

.textDisplay {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.textDisplay.summer i {
  color: orangered;
}

.textDisplay.winter i {
  color: blue;
}

.winter {
  background-color: aliceblue;
}

.summer {
  background-color: orange;
}
